import React, { useState } from 'react';
import { Card, CardContent, Grid, MenuItem, TextField, Typography, useTheme } from '@material-ui/core';
import Chart from 'react-apexcharts';
import barChart from './chart/bar-chart';
import { gridSpacing } from '../../../store/constant';
import axios from 'axios';
import { useEffect } from 'react';

const status = [
    // {
    //     value: 'today',
    //     label: 'Today'
    // },
    {
        value: 'month',
        label: 'This Month'
    },
    {
        value: 'year',
        label: 'This Year'
    }
];

const ChartCard = () => {
    const [value, setValue] = React.useState('month');
    const theme = useTheme();
    var count = 0;
    var count2 = 0;
    const BASE_URL = 'https://backend.alsahaba.sa/api';
    const [consultations, setConsultations] = useState([]);
    const [calls, setCalls] = useState([]);

    const primary = theme.palette.secondary.main;
    barChart.options.grid.borderColor = theme.palette.primary.light;
    barChart.options.yaxis.labels.style.colors = [theme.palette.secondary.main];
    barChart.options.xaxis.labels.style.colors = [primary, primary, primary, primary, primary, primary, primary];
    const fetchConsultations = async () => {
        await axios.get(`${BASE_URL}/consultations/`)
            .then((response) => {
                setConsultations(response.data);
            })
    }
    const fetchCalls = async () => {
        await axios.get(`${BASE_URL}/calls/`)
            .then((response) => {
                setCalls(response.data);
            })
    }
    useEffect(() => {
        fetchConsultations();
        fetchCalls();
    }, []);
    return (
        <Card>
            <CardContent>
                <Grid container spacing={gridSpacing}>
                    <Grid item xs={12}>
                        <Grid container alignItems="center" justifyContent="space-between">
                            <Grid item>
                                <Grid container direction="column" spacing={1}>
                                    <Grid item>
                                        <Typography variant="subtitle2">Requested Consultations</Typography>
                                    </Grid>
                                    <Grid item>
                                        <Typography variant="h3">
                                            <div style={{ display: "none" }}>
                                                {calls?.map((call) => (
                                                    !call.is_new ?
                                                        count++
                                                        :
                                                        ""
                                                ))}
                                            </div>
                                            {count}
                                        </Typography>
                                    </Grid>
                                    <Grid item>
                                        <Typography variant="subtitle2">Accepted Consultations</Typography>
                                    </Grid>
                                    <Grid item>
                                        <Typography variant="h3">
                                            <div style={{ display: "none" }}>
                                                {calls?.map((call) => (
                                                    call ?
                                                        count2++
                                                        :
                                                        ""
                                                ))}
                                            </div>
                                            {count2}
                                        </Typography>
                                    </Grid>
                                </Grid>
                            </Grid>
                            <Grid item>
                                <TextField
                                    id="standard-select-currency"
                                    select
                                    value={value}
                                    onChange={(e) => setValue(e.target.value)}
                                    variant="outlined"
                                >
                                    {status.map((option) => (
                                        <MenuItem key={option.value} value={option.value}>
                                            {option.label}
                                        </MenuItem>
                                    ))}
                                </TextField>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item xs={12}>
                        <Chart {...barChart} />
                    </Grid>
                </Grid>
            </CardContent>
        </Card>
    );
};

export default ChartCard;
