import React from 'react';
import {Grid} from '@material-ui/core';

import {gridSpacing} from '../../store/constant';
import PatientsCard from '../../ui-component/cards/PatientsCard';
import DoctorsCard from '../../ui-component/cards/DoctorsCard';
import ChartCard from '../../ui-component/cards/ChartCard';
import PopularCard from '../../ui-component/cards/PopularCard';
import ConsultationsCard from '../../ui-component/cards/ConsultationsCard';
import { useHistory } from 'react-router-dom';

const Dashboard = () => {
    let history = useHistory();
    const routeChange =(route)=> {
        let path = `/${route}`;
        history.push(path);
      }
    return (
        <Grid container spacing={gridSpacing}>
            <Grid item xs={12}>
                <Grid container spacing={gridSpacing}>
                    <Grid item lg={4} md={6} sm={6} xs={12} onClick={()=>routeChange("patients")} style={{cursor:"pointer"}}>
                        <PatientsCard />
                    </Grid>
                    <Grid item lg={4} md={6} sm={6} xs={12} onClick={()=>routeChange("doctors")} style={{cursor:"pointer"}}>
                        <DoctorsCard />
                    </Grid>
                    <Grid item lg={4} md={6} sm={6} xs={12} onClick={()=>routeChange("consultations")} style={{cursor:"pointer"}}>
                        <ConsultationsCard/>
                    </Grid>
                    {/* <Grid item lg={3} md={6} sm={6} xs={12}>
                        <DoctorsCard />
                    </Grid> */}
                    {/* <Grid item lg={4} md={12} sm={12} xs={12}>
                        <Grid container spacing={gridSpacing}>
                            <Grid item sm={6} xs={12} md={6} lg={12}>
                                <TotalIncomePatternCard />
                            </Grid>
                            <Grid item sm={6} xs={12} md={6} lg={12}>
                                <TotalIncomeCard />
                            </Grid>
                        </Grid>
                    </Grid> */}
                </Grid>
            </Grid>
            <Grid item xs={12}>
                <Grid container spacing={gridSpacing}>
                    <Grid item xs={12} sm={12} md={8}>
                        <ChartCard />
                    </Grid>
                    <Grid item xs={12} sm={12} md={4}>
                        <PopularCard />
                    </Grid>
                </Grid>
            </Grid>
        </Grid>
    );
};

export default Dashboard;
