import React, { useState, useRef } from 'react'
import { Avatar, Button, Card, CardHeader, CardActions, CardContent, Divider, Grid, Link, makeStyles, FormControl, TextareaAutosize, InputLabel, Select, MenuItem, Typography, TextField, OutlinedInput, InputAdornment } from '@material-ui/core';
import { IconSearch } from '@tabler/icons';


import KeyboardArrowUpOutlinedIcon from '@material-ui/icons/KeyboardArrowUpOutlined';

import { gridSpacing } from '../../../store/constant';
import axios from 'axios';
import { useEffect } from 'react';
import { Link as RouterLink } from 'react-router-dom';


import MuiTypography from '@material-ui/core/Typography';

import SubCard from '../../../ui-component/cards/SubCard';

const useStyles = makeStyles((theme) => ({
    cardAction: {
        padding: '10px',
        paddingTop: 0,
        justifyContent: 'center'
    },
    primaryLight: {
        color: theme.palette.primary[200],
        cursor: 'pointer'
    },
    divider: {
        marginTop: '12px',
        marginBottom: '12px'
    },
    avatarSuccess: {
        width: '16px',
        height: '16px',
        borderRadius: '5px',
        backgroundColor: theme.palette.success.light,
        color: theme.palette.success.dark,
        marginLeft: '15px'
    },
    successDark: {
        color: theme.palette.success.dark
    },
    avatarError: {
        width: '16px',
        height: '16px',
        borderRadius: '5px',
        backgroundColor: theme.palette.orange.light,
        color: theme.palette.orange.dark,
        marginLeft: '15px'
    },
    errorDark: {
        color: theme.palette.orange.dark
    }
}));

const PopularCard = () => {
    const classes = useStyles();
    const BASE_URL = 'https://backend.alsahaba.sa/api';
    const [consultations, setConsultations] = useState([]);
    const [selectedConsultation, setSelectedConsultation] = React.useState([]);
    const [visible, setVisibility] = React.useState(false);
    const [visibleCreateConsultation, setVisiblityCreateConsultation] = React.useState(false);
    const [users, setUsers] = React.useState([]);


    const fetchUsers = async () => {
        await axios.get(`${BASE_URL}/users/`).then((response) => {
            setUsers(response.data)
        })
    }
    const fetchConsultations = async () => {
        await axios.get(`${BASE_URL}/consultations/`)
            .then((response) => {
                setConsultations(response.data.reverse());
            })
    }
    const closePopup = () => {
        setVisibility(false);
    };
    const showSelectedConsultation = (consultaion) => {
        setVisibility(true)
        setSelectedConsultation(consultaion)
    }
    const formatDate = (date) => {
        const options = { day: '2-digit', month: 'short', year: 'numeric', hour: '2-digit', minute: '2-digit' };
        const formattedDate = new Date(date).toLocaleDateString('en-US', options);

        // Split the formatted date into day, month, and year parts
        const [month, day, year, hour, minute] = formattedDate.split(' ');

        // Convert the month abbreviation to uppercase
        const capitalizedMonth = month.toUpperCase();

        // Return the formatted date with uppercase month abbreviation and desired format
        return `${day} ${capitalizedMonth} ${year} ${hour} ${minute}`;
    }
    const showCreatePopUp = () => {
        setVisiblityCreateConsultation(true)
    }
    const hideCreatePopUp = () => {
        setVisiblityCreateConsultation(false);
    };
    useEffect(() => {
        fetchConsultations();
        fetchUsers();
    }, []);
    return (
        <Card>
            {visible && <ShowSelectedConsultation consultation={selectedConsultation} handleClose={closePopup} />}
            {visibleCreateConsultation && <CreateConsultation handleClose={hideCreatePopUp} />}
            <CardContent>
                <Grid container spacing={gridSpacing}>
                    <Grid item xs={12}>
                        <Grid container alignContent="center" justifyContent="space-between">
                            <Grid item>
                                <Typography variant="h4">Recent Consultations</Typography>
                            </Grid>
                            <Grid item>
                                <Button
                                    variant="contained"
                                    color="primary"
                                    disableElevation
                                    onClick={showCreatePopUp}
                                >
                                    Create Consultation
                                </Button>
                            </Grid>
                        </Grid>
                    </Grid>

                    <Grid item xs={12}>
                        {consultations.slice(0, 4).map((consultation) => (
                            <Grid container direction="column" className='consultations' key={consultation.id} onClick={() => showSelectedConsultation(consultation)}>
                                <Grid item>
                                    <Grid container alignItems="center" justifyContent="space-between">
                                        <Grid item>
                                            <Typography variant="subtitle1" color="inherit">
                                                {consultation.chief_complaint}
                                            </Typography>
                                        </Grid>
                                        <Grid item>
                                            <Grid container alignItems="center" justifyContent="space-between">
                                                <Grid item>
                                                    <Typography variant="subtitle1" color="inherit" style={{ position: 'relative', top: '13px' }}>
                                                        {users.map((user) => (
                                                            consultation.patient_id == user.id ?
                                                                <>
                                                                    {user.full_name}
                                                                    <br />
                                                                    <strong style={{ fontSize: '12px', color: '#993f95' }}>{formatDate(consultation.created_at)}</strong>
                                                                </>
                                                                :
                                                                ""
                                                        ))}
                                                    </Typography>
                                                </Grid>
                                                <Grid item>
                                                    <Avatar variant="rounded" className={classes.avatarSuccess}>
                                                        <KeyboardArrowUpOutlinedIcon fontSize="small" color="inherit" />
                                                    </Avatar>
                                                </Grid>
                                            </Grid>
                                        </Grid>
                                    </Grid>
                                </Grid>
                                <Grid item>
                                    <Typography variant="subtitle2" className={classes.successDark}>
                                        {users.map((user) => (
                                            consultation.doctor_id == user.id ?
                                                user.full_name
                                                :
                                                ""
                                        ))}
                                    </Typography>
                                </Grid>
                                <Divider className={classes.divider} />
                            </Grid>
                        ))}
                    </Grid>
                </Grid>
            </CardContent>
            <CardActions className={classes.cardAction}>
                <Link component={RouterLink} to={"/consultations"}>
                    View All
                </Link>
            </CardActions>
        </Card>
    );
};

export default PopularCard;

