const useCustomStyles = makeStyles({
    table: {
        minWidth: 350
    },
    searchControl: {
        width: '100%',
        paddingRight: '8px',
        paddingLeft: '16px',
        marginBottom: '16px',
        marginTop: '16px'
    }
});
const CreateConsultation = (props) => {
    const classes = useCustomStyles();
    const BASE_URL = 'https://backend.alsahaba.sa/api';
    const [users, setUsers] = React.useState([]);
    const [patientSearchResult, setPatientSearchResult] = React.useState('');
    const [show, setShow] = React.useState(false);
    const [showPatient, setShowPatient] = React.useState(false);
    const [doctorSearchResult, setDoctorSearchResult] = React.useState('');
    const [patientId, setPatientId] = React.useState(null);
    const [doctorId, setDoctorId] = React.useState(null);

    const [visible, setVisible] = useState(false);
    const fetchUsers = async () => {
        await axios.get(`${BASE_URL}/users/`).then((response) => {
            setUsers(response.data)
        })
    }
    const Popup = ({ handleClose }) => {
        const modalRef = useRef(null);

        const closeWithAnimation = () => {
            if (modalRef.current) {
                modalRef.current.classList.add("closing");
                modalRef.current.classList.remove("closing");
                handleClose();
            }
        }; return (
            <div ref={modalRef} className="graphpop">
                <div className="content">
                    <div className="logo-img">
                        <a href="/">
                            <img src={process.env.PUBLIC_URL + "/images/double-check.gif"} alt="" />
                        </a>
                    </div>
                    <h2><span style={{ color: "#ba8abb" }}>Consultation</span> saved <span style={{ color: "#24ab94" }}>successfully!</span></h2>
                    <div className="cancel-btn">
                        <img src={process.env.PUBLIC_URL + "/images/cancel.png"} id="cancel-here" onClick={closeWithAnimation} />
                    </div>
                    <div style={{ display: 'none' }}>{setTimeout(() => {
                        closeWithAnimation()
                    }, 2000)}
                    </div>
                </div>
            </div>
        );
    };
    const fetchSearch = async (user) => {
        await axios.get(`${BASE_URL}/search?query=${user === "patient" ? patientSearchResult : doctorSearchResult}`).then((response) => {
            setUsers(response.data)
        })
    }

    const handleSubmit = async (e) => {
        e.preventDefault();

        console.log(doctorId)
        console.log(patientId)
        var bodyFormData = new FormData();
        bodyFormData.append("doctor_id", doctorId);
        bodyFormData.append("patient_id", patientId);
        bodyFormData.append("chief_complaint", e.target.chief_complaint.value);
        bodyFormData.append("history_of_illness", e.target.history_of_illness.value);
        bodyFormData.append("review_of_systems", e.target.review_of_systems.value);
        bodyFormData.append("examination", e.target.examination.value);
        bodyFormData.append("assessment", e.target.assessment.value);
        if (e.target.medication == undefined) {
            bodyFormData.append("medication", "No");
        }
        else {
            bodyFormData.append("medication", e.target.medication.value);
        }
        if (e.target.sick_leave == undefined) {
            bodyFormData.append("sick_leave", 0);
        }
        else {
            bodyFormData.append("sick_leave", e.target.sick_leave.value);
        }
        console.log(bodyFormData)
        await axios({
            method: "post",
            url: `${BASE_URL}/create-consultation/`,
            data: bodyFormData,
            headers: { "Content-Type": "multipart/form-data" },
        })
            .then(function (response) {
                showPopup()
                console.log(response);
            })
            .catch(function (response) {
                console.log(response);
            });
    }
    const showPopup = () => {
        setVisible(true);
    };
    const closePopup = () => {
        setVisible(false);
    };
    const handleSearchPatients = (event) => {
        setShowPatient(true)
        console.log(event.target.value)
        if (event.target.value === "") {
            setPatientSearchResult(event.target.value);
            fetchUsers();
        }
        else {
            setPatientSearchResult(event.target.value);
            fetchSearch("patient");
        }

    };
    const handleDoctorInputHide = (iqama_number, doctor_id) => {
        setDoctorSearchResult(iqama_number)
        setDoctorId(doctor_id)
        setShow(false)
    }
    const handlePatientInputHide = (iqama_number, patient_id) => {
        setPatientSearchResult(iqama_number)
        setPatientId(patient_id)
        setShowPatient(false)
    }
    const handlSearcheDoctors = (event) => {
        console.log(event.target.value)
        setShow(true)
        if (event.target.value === "") {
            setDoctorSearchResult(event.target.value);
            fetchUsers();
        }
        else {
            setDoctorSearchResult(event.target.value);
            fetchSearch("doctor");
        }

    };
    useEffect(() => {
        fetchUsers();
    }, []);
    return (
        <Grid container spacing={gridSpacing}>
            <Grid item xs={12}>
                <Card className='popup-container'>
                    <CardHeader title="Consultation" />
                    <Divider />
                    <CardContent className="popup-body overflowY" id="popupCustom">
                        <Button id="closeBtn" onClick={() => props.handleClose()}>X</Button>
                        <Grid container spacing={gridSpacing}>
                            <Grid item xs={12} sm={6}>
                                <SubCard title="Create Consultation">
                                    <form onSubmit={handleSubmit}>
                                        <Grid container direction="column" id="poppins" spacing={1} className='overflowY'>
                                            <Grid item>
                                                <FormControl fullWidth className='mt-5'>
                                                    <InputLabel id="demo-simple-select-label">Doctor's Iqama Number:</InputLabel>

                                                    <OutlinedInput
                                                        className={classes.searchControl}
                                                        id="input-search-profile"
                                                        name="doctor_id"
                                                        value={doctorSearchResult}
                                                        onChange={handlSearcheDoctors}
                                                        placeholder="Search Doctors"
                                                        startAdornment={
                                                            <InputAdornment position="start">
                                                                <IconSearch stroke={1.5} size="1.3rem" className={classes.startAdornment} />
                                                            </InputAdornment>
                                                        }
                                                        aria-describedby="search-helper-text"
                                                        inputProps={{
                                                            'aria-label': 'weight'
                                                        }}
                                                        labelWidth={0}
                                                    />
                                                    {doctorSearchResult && <>
                                                        {users?.filter(user => user.is_doctor).map((doctor) => (
                                                            show &&
                                                            <MenuItem key={doctor.id} onClick={() => handleDoctorInputHide(doctor.iqama_number, doctor.id)} >{doctor.iqama_number}</MenuItem>
                                                        ))}
                                                    </>
                                                    }
                                                </FormControl>
                                            </Grid>
                                            <Grid item>
                                                <FormControl fullWidth className='mt-5'>
                                                    <InputLabel id="demo-simple-select-label">Patient's Iqama Number:</InputLabel>
                                                    <OutlinedInput
                                                        className={classes.searchControl}
                                                        id="input-search-profile"
                                                        value={patientSearchResult}
                                                        onChange={handleSearchPatients}
                                                        placeholder="Search patients"
                                                        startAdornment={
                                                            <InputAdornment position="start">
                                                                <IconSearch stroke={1.5} size="1.3rem" className={classes.startAdornment} />
                                                            </InputAdornment>
                                                        }
                                                        aria-describedby="search-helper-text"
                                                        inputProps={{
                                                            'aria-label': 'weight'
                                                        }}
                                                        labelWidth={0}
                                                    />
                                                    {patientSearchResult && <>
                                                        {users.filter(user => !user?.is_doctor).map((patient, index) => (
                                                            showPatient &&
                                                            <MenuItem key={patient.id} value={patient.id} name="patient_id" onClick={() => handlePatientInputHide(patient.iqama_number, patient.id)} >{patient.iqama_number}</MenuItem>
                                                        ))}
                                                    </>
                                                    }

                                                </FormControl>
                                            </Grid>
                                            <Grid item>
                                                <FormControl fullWidth className='mt-5'>
                                                    <InputLabel id="demo-textarea-select-label">Chief Complaint:</InputLabel>
                                                    <TextareaAutosize
                                                        className='mt-2'
                                                        defaultValue={""}
                                                        name='chief_complaint'
                                                    >
                                                    </TextareaAutosize>
                                                </FormControl>
                                            </Grid>
                                            <Grid item>
                                                <FormControl fullWidth className='mt-5'>
                                                    <InputLabel id="demo-textarea-select-label">History of illness:</InputLabel>
                                                    <TextareaAutosize
                                                        className='mt-2'
                                                        defaultValue={""}
                                                        name='history_of_illness'
                                                    >
                                                    </TextareaAutosize>
                                                </FormControl>
                                            </Grid>
                                            <Grid item>
                                                <FormControl fullWidth className='mt-5'>
                                                    <InputLabel id="demo-textarea-select-label">Review of systems:</InputLabel>
                                                    <TextareaAutosize
                                                        className='mt-2'
                                                        defaultValue={""}
                                                        name='review_of_systems'
                                                    >
                                                    </TextareaAutosize>
                                                </FormControl>
                                            </Grid>
                                            <Grid item>
                                                <FormControl fullWidth className='mt-5'>
                                                    <InputLabel id="demo-textarea-select-label">Examination:</InputLabel>
                                                    <TextareaAutosize
                                                        className='mt-2'
                                                        defaultValue={""}
                                                        name='examination'
                                                    >
                                                    </TextareaAutosize>
                                                </FormControl>
                                            </Grid>
                                            <Grid item>
                                                <FormControl fullWidth className='mt-5'>
                                                    <InputLabel id="demo-textarea-select-label">Assessment:</InputLabel>
                                                    <TextareaAutosize
                                                        className='mt-2'
                                                        defaultValue={""}
                                                        name='assessment'
                                                    >
                                                    </TextareaAutosize>
                                                </FormControl>
                                            </Grid>
                                            <Grid item>
                                                <FormControl fullWidth className='mt-5'>
                                                    <InputLabel id="demo-textarea-select-label">Medication:</InputLabel>
                                                    <TextareaAutosize
                                                        className='mt-2'
                                                        defaultValue={""}
                                                        name='medication'
                                                    >
                                                    </TextareaAutosize>
                                                </FormControl>
                                            </Grid>
                                            <Grid item>
                                                <FormControl fullWidth className='mt-5'>
                                                    <InputLabel id="demo-textarea-select-label">Sick Leave:</InputLabel>
                                                    <TextField
                                                        className='mt-2'
                                                        defaultValue={""}
                                                        type='number'
                                                        name='sick_leave'
                                                    >
                                                    </TextField>
                                                </FormControl>
                                            </Grid>
                                            <Grid item>
                                                <FormControl className='mt-5'>
                                                    <Button
                                                        variant="contained"
                                                        color="primary"
                                                        disableElevation
                                                        type='submit'
                                                    >
                                                        Create Consultation
                                                    </Button>
                                                </FormControl>
                                            </Grid>
                                        </Grid>
                                    </form>
                                </SubCard>
                            </Grid>
                        </Grid>
                    </CardContent>
                </Card>
            </Grid>
            {visible && <Popup handleClose={closePopup} />}
        </Grid>
    );
};